<template>
  <div class="moneyManagement">
    <div class="top">
      <div class="flex">
        <div class="left">
          <p class="label">我的资金（元）</p>
          <p class="value">{{moneyList.length?moneyList[0].toFixed(2):'0.00'}}</p>
        </div>
        <div class="right">
          <div class="btn" @click="$router.push({name:'withdrawal',query:{money:moneyList[0]}})">立即提现</div>
        </div>
      </div>
      <div class="earnings">
        <div class="left">
          <p class="label">今日收入（元）</p>
          <p class="value">{{moneyList.length?moneyList[1].toFixed(2):'0.00'}}</p>
        </div>
        <div class="right">
          <p class="label">累计收入（元）</p>
          <p class="value">{{moneyList.length?moneyList[2].toFixed(2):'0.00'}}</p>
        </div>
      </div>
    </div>
    <div class="mid">
      <div class="mid-top">
        <div class="flex">
          <p class="left" @click="showDate = true">{{date}}</p>
          <img class="icon" src="../../assets/img/icon/down.png" alt @click="showDate = true" />
        </div>
        <div class="flex" v-if="false">
          <p class="flex-2">
            收入
            <span style="color:#eb5252">¥{{total_increase_money.toFixed(2)}}</span>
          </p>
          <p class="flex-2">
            支出
            <span style="color:#000">¥{{total_decrease_money.toFixed(2)}}</span>
          </p>
          <!-- <div class="flex-1 right">
            <p>| 月账单</p>
            <img src="../../assets/img/icon/left.png" alt>
          </div>-->
        </div>
      </div>
      <div class="list mescroll" id="list">
        <div id="listData">
          <div class="item" v-for="(item,index) in list" :key="index">
            <div class="left">
              <p class="title">{{item.cname}}</p>
              <p class="text">{{item.acDes}}</p>
              <p class="time">{{item.createDate.substring(0,16)}}</p>
            </div>
            <div class="right">
              <p class="price" :style="item.cmath == -1?'color:#333':''">{{item.cmath == -1?('-' + item.acMoney.toFixed(2)):('+' + item.acMoney.toFixed(2))}}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <van-popup v-model="showDate" position="bottom">
      <van-datetime-picker v-model="currentDate" type="year-month" :formatter="formatter" @confirm="dateConfirm" @cancel="showDate = false" />
    </van-popup>
  </div>
</template>
<script>
import { Popup, DatetimePicker } from "vant";
import { getBalanceDetails, getBalanceList } from "@/api";
import MeScroll from "mescroll.js";
export default {
  components: {
    [Popup.name]: Popup,
    [DatetimePicker.name]: DatetimePicker
  },
  data() {
    return {
      showDate: false,
      currentDate: new Date(),
      date: "",
      moneyList: [],
      total_decrease_money: 0,
      total_increase_money: 0,
      list: [],
      mescroll: null
    };
  },
  methods: {
    formatter(type, value) {
      if (type === "year") {
        return `${value}年`;
      } else if (type === "month") {
        return `${value}月`;
      }
      return value;
    },
    dateConfirm(value) {
      let year = new Date(value).getFullYear(),
        month = new Date(value).getMonth() + 1,
        day = new Date(value).getDate(),
        hour = new Date(value).getHours(),
        min = new Date(value).getMinutes();
      this.date = `${year}年${month < 10 ? "0" + month : month}月`;
      this.showDate = false;
      this.mescroll.scrollTo(0, 0);
      this.mescroll.resetUpScroll();
    },
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      getBalanceDetails().then(res => {
        loading.clear();
        if (res.code == 0) {
          this.moneyList = res.data.list;
        } else {
          this.$toast(res.msg);
        }
      });
    },
    loadList(pageNum, pageSize, successCallback, errorCallback) {
      let $this = this;
      let dataObj = {
        pageNum: pageNum,
        pageSize: pageSize,
        date: this.date
      };
      getBalanceList(dataObj)
        .then(res => {
          if (res.code == 0) {
            successCallback && successCallback(res.data.page);
          } else {
            $this.$toast.fail(res.errorMsg);
          }
        })
        .catch(res => {
          errorCallback && errorCallback();
          console.log(res);
        });
    },
    startMescroll() {
      this.mescroll = new MeScroll("list", {
        //在vue的mounted生命周期初始化mescroll,确保此处配置的id能够被找到
        up: {
          callback: this.upCallback, //上拉回调
          //以下参数可删除,不配置
          isBounce: false, //此处禁止ios回弹,解析(务必认真阅读,特别是最后一点): http://www.mescroll.com/qa.html#q10
          page: { size: 10 }, //可配置每页8条数据,默认10
          empty: {
            //配置列表无任何数据的提示
            warpId: "listData",
            icon: require("@/assets/img/nodata.png"),
            tip: "暂无数据"
            //						  	btntext : "去逛逛 >" ,
            //						  	btnClick : function() {
            //						  		alert("点击了去逛逛按钮");
            //						  	}
          },
          noMoreSize: 10,
          scrollbar: { use: false },
          htmlNodata: '<p class="upwarp-nodata">-- 无更多数据 --</p>',
          lazyLoad: {
            use: true // 是否开启懒加载,默认false
          }
        }
      });
    },
    //上拉回调 page = {num:1, size:10}; num:当前页 ,默认从1开始; size:每页数据条数,默认10
    upCallback: function(page) {
      //联网加载数据
      let $this = this;
      $this.loadList(
        page.num,
        page.size,
        function(curPageData) {
          //curPageData=[]; //打开本行注释,可演示列表无任何数据empty的配置

          //如果是第一页需手动制空列表 (代替clearId和clearEmptyId的配置)
          console.log(curPageData);
          if (page.num == 1) $this.list = [];
          if (curPageData.list) {
            curPageData.list.map(item => {
              $this.list.push(item);
            });
          }
          //联网成功的回调,隐藏下拉刷新和上拉加载的状态;
          //mescroll会根据传的参数,自动判断列表如果无任何数据,则提示空;列表无下一页数据,则提示无更多数据;
          //   console.log("page.num="+page.num+", page.size="+page.size+", curPageData.length="+curPageData.rows.length+", $this.goodsList.length==" + $this.pdlist.length);
          console.log(page);

          //方法二(推荐): 后台接口有返回列表的总数据量 totalSize
          $this.mescroll.endBySize(
            curPageData.list ? curPageData.list.length : 0,
            curPageData.count
          ); //必传参数(当前页的数据个数, 总数据量)
        },
        function() {
          //联网失败的回调,隐藏下拉刷新和上拉加载的状态;
          $this.mescroll.endErr();
        }
      );
    }
  },
  mounted() {
    this.date = `${new Date().getFullYear()}年${
      new Date().getMonth() + 1 < 10
        ? "0" + (new Date().getMonth() + 1)
        : new Date().getMonth() + 1
    }月`;
    this.loadData();
    this.startMescroll();
  }
};
</script>
<style lang="scss" scoped>
.moneyManagement {
  .top {
    background: url("../../assets/img/icon/account.png") no-repeat;
    background-size: 100% 100%;
    margin: 15px;
    border-radius: 5px;
    .flex {
      display: flex;
      padding: 15px;
      color: #fff;
      align-items: center;
      .left {
        flex: 1;
        .label {
          font-size: 13px;
          line-height: 40px;
        }
        .value {
          font-size: 30px;
          line-height: 40px;
        }
      }
      .right {
        flex: 1;
        .btn {
          width: 80px;
          height: 35px;
          line-height: 35px;
          border-radius: 5px;
          background: #fff;
          color: #eb5252;
          float: right;
          text-align: center;
          font-size: 13px;
          font-weight: bold;
        }
      }
    }
    .earnings {
      display: flex;
      padding: 15px;
      color: #fff;
      align-items: center;
      .left {
        flex: 1;
      }
      .right {
        flex: 1;
      }
      .label {
        font-size: 13px;
        line-height: 25px;
      }
      .value {
        font-size: 20px;
        line-height: 25px;
      }
    }
  }
  .mid {
    margin-top: 25px;
    &-top {
      border-bottom: 1pt solid #e1e1e1;
      box-sizing: border-box;
      padding-bottom: 20px;
      .flex {
        display: flex;
        padding: 5px 15px;
        align-items: center;
        .left {
          font-size: 18px;
          font-weight: bold;
        }
        .icon {
          width: 13px;
          margin-left: 5px;
        }
        .flex-2 {
          flex: 2;
          color: #666;
        }
        .right {
          width: 60px;
          display: flex;
          align-items: center;
          font-size: 13px;
          color: #999;
          img {
            width: 5px;
            margin-left: 5px;
          }
        }
      }
    }
    .list {
      height: calc(100vh - 190px - 30px - 55px);
      .item {
        padding: 10px 15px;
        display: flex;
        align-items: center;
        border-bottom: 1pt solid #e1e1e1;
        .left {
          flex: 2;
          line-height: 25px;
          .title {
            font-weight: bold;
          }
          .text {
            font-size: 15px;
          }
          .time {
            color: #999;
            font-size: 13px;
          }
        }
        .right {
          text-align: right;
          .price {
            font-size: 18px;
            color: $base-color;
          }
        }
      }
    }
  }
}
</style>
<style lang="scss">
.moneyManagement {
  .mescroll-empty {
    transform: translateY(30%);
  }
}
</style>

